import React from 'react';
import { useState } from 'react';
import './App.css';

const App = () => {
	const [result, setResult] = useState('');

	const handleClick = e => {
		if (result.length > 13) {
			e.target.style.backgroundColor = 'rgba(253, 44, 29, 0.5)';
			setTimeout(() => {
				if (e.target.classList.contains('highlight')) {
					e.target.style.backgroundColor = 'rgba(86, 203, 219, 0.5)';
				} else {
					e.target.style.backgroundColor = 'rgba(255, 255, 255, 0.05)';
				}
			}, 100);
			return;
		}
		setResult(prev => (prev += e.target.name));
	};

	const clear = () => setResult('');

	const backspace = () => {
		setResult(result.slice(0, -1));
	};

	const calculate = () => {
		try {
			// eslint-disable-next-line
			if (Number.isInteger(eval(result))) {
				// eslint-disable-next-line
				setResult(eval(result).toString());
			} else {
				// eslint-disable-next-line
				setResult(eval(result).toFixed(2).toString());
			}
		} catch (err) {
			setResult('Error');
		}
	};

	return (
		<>
			<div className='container'>
				<form>
					<input type='text' value={result} readOnly={true} />
				</form>

				<div className='keypad'>
					<button onClick={clear} id='clear' className='highlight'>
						Clear
					</button>
					<button onClick={backspace} id='backspace' className='highlight'>
						C
					</button>
					<button name='/' onClick={handleClick} className='highlight'>
						&divide;
					</button>
					<button name='7' onClick={handleClick}>
						7
					</button>
					<button name='8' onClick={handleClick}>
						8
					</button>
					<button name='9' onClick={handleClick}>
						9
					</button>
					<button name='*' onClick={handleClick} className='highlight'>
						&times;
					</button>
					<button name='4' onClick={handleClick}>
						4
					</button>
					<button name='5' onClick={handleClick}>
						5
					</button>
					<button name='6' onClick={handleClick}>
						6
					</button>
					<button name='-' onClick={handleClick} className='highlight'>
						&ndash;
					</button>
					<button name='1' onClick={handleClick}>
						1
					</button>
					<button name='2' onClick={handleClick}>
						2
					</button>
					<button name='3' onClick={handleClick}>
						3
					</button>
					<button name='+' onClick={handleClick} className='highlight'>
						+
					</button>
					<button name='0' onClick={handleClick}>
						0
					</button>
					<button name='.' onClick={handleClick}>
						.
					</button>
					<button onClick={calculate} id='result' className='highlight'>
						=
					</button>
				</div>
			</div>
		</>
	);
};

export default App;
